@import url(https://fonts.googleapis.com/css2?family=Courier+Prime&family=Inter:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  height: 100%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Main {
  overflow-x: hidden;
}
.Main__Header {
  width: 100%;
  background-color: rgb(47, 95, 253);
  height: 10vh;
  color: white;
  font-size: 20px;
  display: flex;
  padding-left: 10px;
  align-items: center;
  font-size: 25px;
}
.Main__Body {
  height: 90vh;
  width: 100%;
}
.Main__TextArea {
  height: 90%;
  width: 100%;
  padding-top: 5px;
  flex-direction: column;
  flex-wrap: nowrap;
  overflow-y: scroll;
  overflow-x: hidden;
}
@media only screen and (min-width: 800px) {
  .Main__TextArea {
    margin-bottom: 10px;
  }
  .Main__Header {
    height: 9vh;
  }
  .Main__TextArea {
    height: 86%;
  }
}
.Main__Input {
  height: 8%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Main__OwnMessContainer {
  border-radius: 20px;
  padding: 5px 12px;
  background-color: rgb(68, 112, 255);
  color: rgb(241, 241, 241);
  display: inline-block;
  margin-right: 5px;
  max-width: 70%;
  font-size: 25px;
}
.Main__RespMessContainer {
  border-radius: 20px;
  padding: 4px 12px;
  background-color: rgb(197, 197, 197);
  display: inline-block;
  max-width: 70%;
  margin-left: 5px;
  font-size: 25px;
}
.Main__MessContainer {
  position: relative;
  display: flex;
  min-height: 50px;
  width: 100%;
}
.Main__MessOwn {
  justify-content: flex-end;
}

.Main__BotImage {
  width: 40px;
  margin-right: 8px;
}

.FP__Body {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.FP__Image {
  width: 200px;
  height: 200px;
  margin-top: 10vh;
}

.FP__HeadText {
  font-family: "Courier Prime";
  font-size: 45px;
}
.FP__SubText {
  font-family: "Inter";
  font-size: 23px;
  margin-top: -20px;
  text-align: center;
}
.FP__Proceed {
  border: 1px solid rgb(68, 68, 68);
  border-radius: 10px;
  padding: 15px 15px;
  font-family: "Inter";
  font-size: 27px;
}
.FP__ProceedLink {
  margin-top: 20vh;
}
.FP__AL {
  color: rgb(66, 66, 66);
  margin-top: 5vh;
  font-size: 22px;
}
.FP__Link {
  text-decoration: none;
  color: black;
}

